.App-header {
  background-color: linear-gradient( #FEB100, #FB1400);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.content-container {
  background-color: linear-gradient('to right', rgba(0,0,0,0.5) 0%, rgba(0,0,0,0.5) 100%, rgba(0,0,0,0.5) 0%);
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
